.aboutchoosesearch {
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    background-color: white;
}
h{
    font-weight: bold;
}
.left, .right {
    flex: 1;
    padding: 10px;
}

.AboutChooseSearchContainer {
    /* Adjustments to AboutContainer for responsiveness */
    background: url(https://i.ibb.co/9wgsYMx/images.png);
    width: 500px;
    height: 500px;
    max-width: 550px;
    margin: auto;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-size: cover;
    position: relative;
}
  /* H2 styling */
  h2 {
    color: #0d6efd; /* Bootstrap primary color for example */
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  /* Paragraph styling */
  p {
    margin-bottom: 15px;
    color: #666;
  }
  
  /* Images within paragraphs */
  p img {
    margin-right: 10px;
    vertical-align: middle; /* Aligns the image nicely with the text */
  }
  
  /* H5 styling for smaller headings */
  h5 {
    font-size: 18px;
    color: #333;
    margin-bottom: 15px;
  }
  
  /* Specific class styling for images */
  .one {
    width: 20px; /* Example size, adjust as needed */
    height: auto;
  }

@media (max-width: 768px) {
    .left, .right {
        width: 100%;
    }

    .AboutChooseSearchContainer {
        height: auto;
    }
    h2 {
        font-size: 20px; /* Slightly smaller on tablets */
        margin-bottom: 15px;
      }
    
      h5 {
        font-size: 16px; /* Adjust for smaller screens */
      }
    
      .one {
        width: 15px; /* Smaller images for smaller screens */
      }
    
      p, h5 {
        margin-bottom: 10px; /* Less space on mobile */
      }
}

@media (max-width: 480px) {
    .AboutChooseSearchContainer {
        width: 100%; /* Takes a bit less space */
        height: 100%;
    }
    h2 {
        font-size: 8px; /* Even smaller on phones */
        margin-bottom: 10px;
      }
    
      h5 {
        font-size: 6px; /* Adjust for very small screens */
      }
    
      .one {
        width: 10px; /* Even smaller images for phones */
      }
  
}